export class BrowserSupport {
    static ROOT = typeof window === "undefined" ? global : window;
    static DOC = this.ROOT.document;
    static passive = this.testPassive();
    static DIVStyle = this.DOC.createElement("div").style;
    static CSSVendor = this.parseVendor();
    static opacity = this.CSSTest("opacity", "opacity");
    static transform = this.CSSTest("transform", "transform");
    static perspective = this.CSSTest("perspective", "perspective");
    static transformStyle = this.CSSTest("transform-style", "transformStyle");
    static transformOrigin = this.CSSTest("transform-origin", "transformOrigin");
    static backfaceVisibility = this.CSSTest("backface-visibility", "backfaceVisibility");
    static preserve3D = this.detectPreserve3D();
    static CLASS_TO_TYPE = this.mapTypes();
    static parseVendor() {
        let prop;
        const tests = "-webkit- -moz- -o- -ms-".split(" ");
        while ((prop = tests.shift())) {
            if (this.camelCase(`${prop}transform`) in this.DIVStyle) {
                return prop;
            }
        }
        return "";
    }
    static CSSTest(name, _declaration) {
        const prop = this.camelCase(name);
        const _prop = this.camelCase(this.CSSVendor + prop);
        return ((prop in this.DIVStyle && prop) ||
            (_prop in this.DIVStyle && _prop) ||
            "");
    }
    static detectPreserve3D() {
        if (this.transformStyle) {
            this.DIVStyle[this.transformStyle] = "preserve-3d";
            return true;
        }
        return false;
    }
    static camelCase(str) {
        return `${str}`
            .replace(/^-ms-/, "ms-")
            .replace(/-([a-z]|[0-9])/gi, (_, letter) => {
            return `${letter}`.toUpperCase();
        });
    }
    static getStyle(element, prop) {
        const style = (this.ROOT.getComputedStyle &&
            this.ROOT.getComputedStyle(element, null)) ||
            element.currentStyle ||
            element.style;
        return style[prop];
    }
    static mapTypes() {
        const map = {};
        this.constructors.forEach((name) => {
            map[`[object ${name}]`] = name.toLowerCase();
        });
        return map;
    }
    static get constructors() {
        return [
            "Boolean",
            "Number",
            "String",
            "Function",
            "Array",
            "Date",
            "RegExp",
            "Object",
            "Error",
        ];
    }
    static testPassive() {
        let supportsPassive = false;
        try {
            const opts = Object.defineProperty({}, "passive", {
                get: function () {
                    supportsPassive = { passive: true };
                },
            });
            this.ROOT.addEventListener("testPassive", () => { }, opts);
        }
        catch (e) {
            //silence
        }
        return supportsPassive;
    }
}
